import {customElement} from '@here/hds-base';
import * as HDSIconLibrary from '@here/hds-iconlibrary';
import {IconCategory} from '@here/hds-iconlibrary';
import {html, LitElement, PropertyValues} from 'lit';
import {property, state} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-icon.css.js';

export type IconStyle = 'solid' | 'outline';
export type IconSvgSize = '8px' | '16px' | '24px';
export type IconNotFoundType = 'FontNotFound' | 'CategoryNotFound' | 'IconNotFound';

export type IconNotFoundPayload = {
  type: IconNotFoundType;
  message: string;
  category: IconCategory;
  name: string;
  style: IconStyle;
  size: IconSvgSize;
};

/**
 * @event {CustomEvent<{type: IconErrorType, message: string, category: IconCategory, name: string, style: IconStyle, size: IconSvgSize}>} notfound Fired when the icon doesn't exist. `IconNotFoundPayload`
 *
 * @cssprop --hds-icon-color  Color value for the icon
 * @cssprop --hds-icon-size   Size value for the icon, overrides the attribute `size`
 *
 * */
@customElement('hds-icon')
export class Icon extends LitElement {
  static styles = styles;
  private static _nameByMediaType: any;
  @property({type: String, reflect: true}) name = '';
  @property({type: String, reflect: true}) category: IconCategory = 'core-ui';
  @property({type: String, attribute: 'icon-style', reflect: true}) iconStyle: IconStyle = 'solid';
  @property({type: String, reflect: true}) size = '16px';
  @state() errorEventPayload?: IconNotFoundPayload;

  static getNameByMediaType(type: string) {
    if (Icon._nameByMediaType === undefined) {
      Icon._nameByMediaType = {
        'image/jpeg': 'JPG-File',
        'image/jpg': 'JPG-File',
        'application/msword': 'DOC-File',
        'application/postscript': 'EPS-File',
        'application/eps': 'EPS-File',
        'application/x-eps': 'EPS-File',
        'image/eps': 'EPS-File',
        'image/x-eps': 'EPS-File',
        'image/gif': 'GIF-File',
        'text/html': 'HTML-File',
        'audio/mpeg': 'MP3-File',
        'application/pdf': 'PDF-File',
        'image/png': 'PNG-File',
        'image/svg+xml': 'SVG-File',
        'image/tiff': 'TIFF-File',
        'text/plain': 'TXT-File',
        'audio/wav': 'WAV-File',
        'audio/x-wav': 'WAV-File',
        'application/zip': 'ZIP-File',
      };
    }

    if (type in Icon._nameByMediaType) return Icon._nameByMediaType[type];
    if (type.endsWith('/json') || type.endsWith('+json')) return 'JSON-File';
    if (type.includes('image/')) return 'image';
    if (type.includes('compressed')) return 'archive';

    return 'file';
  }

  protected async updated(_changedProperties: PropertyValues) {
    if (_changedProperties.has('errorEventPayload')) {
      // HACK: We set the timeout to the next browser loop so this event can be caught by React
      setTimeout(() => {
        this.dispatchEvent(
          new CustomEvent<IconNotFoundPayload>('notfound', {
            detail: this.errorEventPayload,
            bubbles: true,
            composed: true,
          })
        );
      }, 0);
    }
  }

  protected render() {
    // Create a new var to not trigger the update
    let styleIcon = this.iconStyle;
    // Only icons bigger than 24px will be style outline.
    // if an icon smaller we revert to 16px solid.
    const size = parseInt(this.size, 10);
    let svgSize: IconSvgSize = '16px';

    if (size >= 24) {
      svgSize = '24px';
    } else if (size < 16) {
      styleIcon = 'solid';
      svgSize = '8px';
    } else {
      styleIcon = 'solid';
      svgSize = '16px';
    }

    let error: string | undefined;
    let errorType: IconNotFoundType = 'IconNotFound';

    // Check if font has been loaded
    const allIconsFontFamily = `hds-iconlibrary-${svgSize}`;
    const categoryFontFamily = `hds-iconlibrary-${this.category}-${svgSize}`;
    let fontFamily: string | undefined;

    if (document.fonts) {
      for (const value of document.fonts) {
        // Firefox returns the name between double quotes
        const fontFamilyName = value.family.replace(/"([^"]+(?="))"/g, '$1');
        if (!fontFamilyName) continue;
        if (fontFamily && fontFamily === allIconsFontFamily) continue;
        if (fontFamilyName === allIconsFontFamily) {
          fontFamily = allIconsFontFamily;
        } else if (fontFamilyName === categoryFontFamily) {
          fontFamily = categoryFontFamily;
        }
      }
    } else {
      error =
        'Fonts not found in document. Maybe test environment or SSR? Falling back to all icons font family';
      this.errorEventPayload = {
        message: error,
        name: this.name,
        type: 'FontNotFound',
        category: this.category,
        size: svgSize,
        style: styleIcon,
      };
      fontFamily = allIconsFontFamily;
    }

    if (!fontFamily) {
      error = `Font ${categoryFontFamily} has not been found! Make sure font has been loaded properly`;
      errorType = 'FontNotFound';
      fontFamily = categoryFontFamily;
    }
    let codepointsKey: string;
    let codepointKey: string;
    if (fontFamily === allIconsFontFamily) {
      codepointsKey = `HDS_ICONLIBRARY_${svgSize.toUpperCase()}_CODEPOINTS`;
      codepointKey = `${this.category}-${this.name}_${styleIcon}_${svgSize}`;
    } else {
      codepointsKey = `HDS_ICONLIBRARY_${this.category
        .replaceAll('-', '_')
        .toUpperCase()}_${svgSize.toUpperCase()}_CODEPOINTS`;
      codepointKey = `${this.name}_${styleIcon}_${svgSize}`;

      // Check if category exists
      if (!HDSIconLibrary[codepointsKey]) {
        errorType = 'CategoryNotFound';
        error = `Category ${this.category}  not found!`;
      }
    }
    // Check if the icon exists
    const codepoint: number = HDSIconLibrary[codepointsKey][codepointKey];
    if (!codepoint) {
      errorType = 'IconNotFound';
      error = `Icon ${this.category}/${this.name} style ${styleIcon} size ${svgSize} not found!`;
    }
    // During test the font is not loaded. We skipped if the font is not loaded for core-ui
    if (error && !(this.category === 'core-ui' && errorType === 'FontNotFound')) {
      // eslint-disable-next-line no-console
      // console.warn(error);
      this.errorEventPayload = {
        type: errorType,
        message: error,
        category: this.category,
        name: this.name,
        style: styleIcon,
        size: svgSize,
      };
    }
    const cssClasses = {};
    cssClasses[`size-${svgSize}`] = true;
    cssClasses[`style-${styleIcon}`] = true;

    // https://main.gitlab.in.here.com/design/here-design-system/web/hds/-/issues/1013
    // for some reason Lit StyleMap directive not updating [style] on fontFamily change
    const cssInlineStyle = Object.entries({
      '--hds-icon-actual-size': `var(--hds-icon-size, calc(${size}rem / var(--hds-font-baseline)))`,
      'font-family': `${fontFamily}, serif !important`,
    })
      .map(([k, v]) => `${k}:${v}`)
      .join(';');

    return html`
      <i
        class="hds-icon ${classMap(cssClasses)}"
        style="${cssInlineStyle}"
        data-unicode="${Number.isNaN(codepoint) || !codepoint
          ? ''
          : String.fromCodePoint(codepoint)}"
        role="img"
        aria-label="${this.ariaLabel ? this.ariaLabel : this.name}"
      ></i>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-icon': Icon;
  }
}
